import navigateDropdawnFix from "./navigateDropdawnFix";

const $body = document.body

function changeFont(size) {
  console.log('Изменен шрифт');
  size = (16 + Number(size) * 2);
  $body.style.fontSize = size + 'px';
  localStorage.setItem('vvi-font', size);
  navigateDropdawnFix();
}

function removeClassByPrefix(node, prefix) {
  var regx = new RegExp('\\b' + prefix + '[^ ]*[ ]?\\b', 'g');
  node.className = node.className.replace(regx, '');
  return node;
}

function changeTheme(theme) {
  console.log('Изменена тема', theme);

  removeClassByPrefix($body, 'theme--');

  $body.classList.add(theme);

  localStorage.setItem('vvi-theme', theme);

}

function closeVvi() {
  document.querySelector('.vvi').classList.remove('vvi_visible')
  removeClassByPrefix($body, 'theme--');

  localStorage.removeItem('vvi-theme');
  localStorage.removeItem('vvi-font');

  $body.classList.add('theme--default');
  $body.style.fontSize = '16px';
}

function addEventsVvi(){

  document.querySelector('#showVVI').addEventListener("click", () => {
    document.querySelector('.vvi').classList.add('vvi_visible');
  });

  const allvvibtn = document.querySelectorAll(".vvi__btn")
  allvvibtn.forEach((btn) => {
    btn.addEventListener("click", event => {
      if(event.target.dataset.change === 'font'){
        changeFont(event.target.dataset.value)
      }

      if(event.target.dataset.change === 'theme'){
        changeTheme(event.target.dataset.value);
      }

      if(event.target.dataset.change === 'close'){
        closeVvi()
      }
    });
  })
}

export default function () {
  addEventsVvi();
}
