import Header from './Header';
import VVI from './vvi'
import navigateDropdawnFix from "./navigateDropdawnFix";

const vviTheme = localStorage.getItem('vvi-theme');
const vviFont = localStorage.getItem('vvi-font');
const $body = document.body;

vviTheme ? $body.classList.add(vviTheme) : $body.classList.add('theme--default');
vviFont && ($body.style.fontSize = vviFont + 'px');

(vviFont || vviTheme) && (document.querySelector('.vvi').classList.add('vvi_visible'));



document.addEventListener("DOMContentLoaded", function(event) {
  navigateDropdawnFix();
 Header();
 VVI();
});







if (module.hot) {
  module.hot.accept(function () {
    navigateDropdawnFix();
  });
};

const svg = require('./svguse');
svg.svguse();
